.progress {
    padding: 0.75em 1em;
    text-align: center;

    &__item {
        display: inline-block;
        @include size(0.625em);
        border-radius: 50%;
        border: 1px solid $colorOutline;
        margin: 0 0.5em;
        vertical-align: top;

        &--complete {
            border: 0;
            background-color: $colorDarkBG3; }
        &--active {
            border: 0;
            background-color: $colorEmphasis; } } }

.progress-bar {
    height: 2em;
    background-color: $colorDarkBG3;

    &__rail {
        height: 100%;
        width: 0%;
        background-color: $colorEmphasis;
        box-shadow: $shadowDownLight;
        @include transition(width $durationShort); } }
