.container {
    margin: 0 auto;
    max-width: 68em; }

.scroller {
    position: relative;
    height: 100%; }

.content-whiteout {
    @include position(absolute, 0 null null 0);
    @include size(100%);
    opacity: 0;
    z-index: -1;
    background-color: $colorWhiteout;
    @include transition(opacity $durationMedium, z-index 0 ease $durationMediumUp);

    @media screen and (max-width: $thresholdCollapse) {
        &--active {
            z-index: 120;
            opacity: 1;
            @include transition(opacity $durationMedium);
            cursor: pointer; } } }
